

.header{
  height: 50px;
  background-color: #212121;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0 20px;
  justify-content: space-between;

  .textBtn{
    color: white;
    margin-right: 10px;
  }
}

.editorWrap{
  width: 34% !important;
}

.wrapper{
  height: 100%;
  position: relative;
  width: 100%;
}

.question{
  padding: 20px;
}

.main{
  position: absolute;
  top: 50px;
  bottom: 0;
  width: 100%;
  display:flex;
  flex-direction: row;

  > div{
    width: 33%;
    display: inline-block;
    height: 100%;
  }
}